.details {
  display: flex;
}

.details h3 {
  margin: 5px 0px 35px 0px;
  cursor: pointer;
}

.details h3:hover {
  color: #ff4c8b;
}

.share {
  margin: 5px;
}

.details h2 {
  margin: 5px 0px 3px 0px;
}

.image {
  min-width: 300px;
  max-width: 300px;
  margin-right: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.graph-cont {
  position: relative;
  width: 100%;
  margin-top: 30px;
}

.graph {
  margin-top: 30px;
  height: 500px;
  width: 100%;
  min-width: 100%;
}

.graph-loader {
  position: absolute;
  top: 40%;
  left: 50%;
  margin-left: -50px;
}

.graph-err {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -100px;
}

.graph-header {
  display: flex;
  width: 100%;
  height: 50px;
  justify-content: space-between;
  align-items: center;
}

.collection-img {
  width: 100%;
  height: 100%;
}

.collection-display {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}

.nft {
  width: 200px;
  height: 200px;
  cursor: pointer;
  object-fit: contain;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 32px 0px 32px 0px;
  font-weight: bold;
}

.collection-load {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3%;
}

.collection-table {
  margin-top: 20px;
}

.collection-table td,
tr,
th {
  padding: 8px 60px 0px 0px;
  border: none;
}

.collection-table .title-row {
  font-size: 20px;
  color: #00d8d5;
  font-weight: bold;
}

.collection-table .data-row {
  color: #f2f2f2;
  font-size: 24px;
  font-weight: bold;
}
